<template>
  <div class="nft-mart-container">
    <h1>NFT Mart Test Page</h1>
    <div class="auction-house-info">
      <h2>Auction House Info</h2>
      <p><strong>Address:</strong> {{ auctionHouseAddress }}</p>
    </div>
    
    <div class="nft-info">
      <h2>NFT Info</h2>
      <p><strong>Mint Address:</strong> {{ nftMintAddress }}</p>
      <p><strong>Price:</strong> {{ price }} SOL</p>
      <p><strong>Duration:</strong> 24 hours</p>
    </div>

    <div class="actions">
      <button 
        class="create-listing-btn" 
        @click="createListing" 
        :disabled="isCreatingListing"
      >
        {{ isCreatingListing ? 'Creating Listing...' : 'Create Listing' }}
      </button>
      
      <div v-if="txSignature" class="tx-info">
        <p><strong>Transaction Signature:</strong></p>
        <a :href="`https://solscan.io/tx/${txSignature}`" target="_blank">{{ txSignature }}</a>
      </div>
      
      <div v-if="error" class="error">
        <p>{{ error }}</p>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useWallet } from 'solana-wallets-vue';
import { Connection, PublicKey, Transaction, SystemProgram, Keypair } from '@solana/web3.js';
import { getAssociatedTokenAddress } from '@solana/spl-token';
import { 
  Metaplex, 
  walletAdapterIdentity, 
  toMetaplexFile,
  toBigNumber,
  auctionHouseModule,
  sol
} from '@metaplex-foundation/js';
import store from '@/vuex'

// Constants
const auctionHouseAddress = 'G26L5YGdj9PaYuePz2jj7Qez2S3PJ39xGEKbAnoYHfVE';
// Use your NFT mint address
const nftMintAddress = 'bcexaZs8HW4TabokwHVJ2Wnpb7yBw5iJ4JHiHfWJCi4';
const price = 0.1; // SOL
const durationHours = 24;
const endpoint = 'https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c';

// State
const isCreatingListing = ref(false);
const txSignature = ref('');
const error = ref('');

// Get wallet
const { wallet: walletAdapter, publicKey, signTransaction, signAllTransactions, signMessage, connected, sendTransaction } = useWallet();

const wallet = store.getters.walletAddress

// Create connection
const connection = new Connection(endpoint);

// Create Metaplex instance
const metaplex = ref(null);

onMounted(() => {
  if (connected.value) {
    // Используем реальный wallet с модулем Auction House
    metaplex.value = Metaplex.make(connection)
      .use(auctionHouseModule());
  }
});

// Create listing function
async function createListing() {
  if (!connected.value) {
    error.value = 'Please connect your wallet first';
    return;
  }
  
  try {
    isCreatingListing.value = true;
    error.value = '';
    
    // Update Metaplex with current wallet
    console.log('Using wallet address:', wallet);
    // Используем реальный wallet с модулем Auction House
    metaplex.value = Metaplex.make(connection)
      .use(auctionHouseModule());
    
    // Create Auction House client
    const auctionHouseClient = metaplex.value.auctionHouse();
    
    // Find the Auction House
    const auctionHouse = await auctionHouseClient.findByAddress({
      address: new PublicKey(auctionHouseAddress),
    });
    
    console.log('Found Auction House:', auctionHouse);

    console.log('for nft: ', nftMintAddress)
    
    // For testing purposes, we'll skip the NFT check and directly create the listing
    console.log('Skipping NFT metadata check for testing purposes');
    
    // In a real implementation, you would verify the NFT exists with:
    // const nft = await metaplex.value.nfts().findByMint({
    //   mintAddress: new PublicKey(nftMintAddress),
    // });
    // console.log('Found NFT:', nft);
    
    // Calculate listing end time (24 hours from now)
    const endTime = new Date();
    endTime.setHours(endTime.getHours() + durationHours);
    
    // Создаем листинг
    try {
      // Создаем листинг напрямую через Metaplex
      console.log('Создаем листинг через Metaplex...');
      
      // Получаем блокхэш для транзакции
      const { blockhash, lastValidBlockHeight } = await connection.getLatestBlockhash('finalized');
      
      // Создаем транзакцию и инструкции для листинга
      console.log('Создаем транзакцию для листинга...');
      
      // Используем просто auctionHouse.list с wallet
      console.log('Создаем листинг через auctionHouse.list...');

      console.log('Wallet address:', wallet);
      console.log('Public key:', publicKey.value?.toString());
      
      // Создаем правильный адаптер для Phantom
      const phantomAdapter = {
        publicKey: publicKey.value,
        signTransaction: async (tx) => {
          console.log('Signing transaction with Phantom...');
          return await window.solana.signTransaction(tx);
        },
        signAllTransactions: async (txs) => {
          console.log('Signing multiple transactions with Phantom...');
          return await window.solana.signAllTransactions(txs);
        },
        signMessage: async (msg) => {
          console.log('Signing message with Phantom...');
          const encodedMsg = new TextEncoder().encode(msg);
          return await window.solana.signMessage(encodedMsg);
        }
      };
      
      // Создаем новый Metaplex с нашим адаптером
      console.log('Создаем Metaplex с адаптером Phantom...');
      const mx = Metaplex.make(connection)
        .use(walletAdapterIdentity(phantomAdapter))
        .use(auctionHouseModule());


      
      // Получаем адрес кошелька как PublicKey
      const walletPublicKey = new PublicKey(wallet);
      console.log('Wallet public key:', walletPublicKey.toString());

      const tokenAccounts = await connection.getParsedTokenAccountsByOwner(walletPublicKey, { mint: new PublicKey(nftMintAddress) });
      console.log('Token accounts:', tokenAccounts);
      
      // Получаем Associated Token Account для NFT
      const ata = await getAssociatedTokenAddress(
        new PublicKey(nftMintAddress), // mint
        walletPublicKey // owner
      );
      console.log('Associated Token Account (ATA):', ata.toString());

      // Сначала проверяем, существует ли счет ATA
      try {
        console.log('Проверяем существование ATA...');
        
        // Проверяем существование счета
        const accountInfo = await connection.getAccountInfo(ata);
        
        if (accountInfo === null) {
          console.log('ATA не существует. Пробуем продолжить без проверки баланса.');
          // Можно продолжить без проверки баланса, так как Metaplex/AuctionHouse должен проверить владение
        } else {
          console.log('ATA существует, проверяем баланс');
          // Если счет существует, проверяем баланс
          const ataBalance = await connection.getTokenAccountBalance(ata);
          console.log('ATA balance:', ataBalance.value.uiAmount);

          if (!ataBalance.value.uiAmount) {
            console.warn('Баланс ATA равен 0');
          }
        }
      } catch (error) {
        console.error('Error checking ATA:', error);
        // Продолжаем без ошибки, чтобы посмотреть, что будет дальше
        console.log('Продолжаем без проверки ATA...');
      }
      
      // Получаем Auction House клиент
      console.log('Получаем Auction House клиент...');
      const auctionHouseClient = mx.auctionHouse();
      
      // Создаем листинг с помощью high-level API
      console.log('Создаем листинг...', auctionHouse);
      // Добавляем все необходимые опции, чтобы избежать предупреждения
      const { response } = await auctionHouseClient.list({
        auctionHouse,
        mintAccount: new PublicKey(nftMintAddress),
        price: sol(1), // Цена в lamports
        // tokenAccount: null,
        // tokens: 1,
        // authority: new PublicKey(wallet),
        // seller: publicKey.value, // Явно указываем продавца
        // confirmOptions: {
        //   skipPreflight: false, // Обязательно проводим предварительную проверку
        //   commitment: 'finalized', // Максимальный уровень подтверждения
        //   maxRetries: 3, // Количество попыток
        // },
      });
      
      // Получаем подпись транзакции
      const signature = response.signature;
      
      console.log('Транзакция отправлена с подписью:', signature);
      txSignature.value = signature;
      
      // Ждем подтверждения транзакции
      console.log('Ожидаем подтверждения транзакции...');
      const confirmation = await connection.confirmTransaction({
        signature,
        blockhash,
        lastValidBlockHeight,
      }, 'finalized'); // Используем максимальный уровень подтверждения
      
      console.log('Транзакция подтверждена:', confirmation);
      
      // Создаем запись в базе данных
      await createListingRecord(signature);
    } catch (listingError) {
      console.error('Error in auction house listing:', listingError);
      error.value = `Listing error: ${listingError.message}`;
      isCreatingListing.value = false;
      return;
    }
    
    // Moved into try/catch block above
    
  } catch (err) {
    console.error('Error creating listing:', err);
    error.value = `Error: ${err.message}`;
  } finally {
    isCreatingListing.value = false;
  }
}

// Create a record in our database
async function createListingRecord(transactionSignature) {
  try {
    const response = await fetch('/api/nft-market/listings', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        mintAddress: nftMintAddress,
        initialPrice: price,
        instantPrice: price,
        endTime: new Date(Date.now() + durationHours * 60 * 60 * 1000).toISOString(),
        sellerWallet: publicKey.value.toString(),
        transactionSignature
      }),
    });
    
    const data = await response.json();
    
    if (!data.success) {
      throw new Error(data.message || 'Failed to create listing record');
    }
    
    console.log('Listing record created:', data);
  } catch (err) {
    console.error('Error creating listing record:', err);
    error.value = `Error creating record: ${err.message}`;
  }
}
</script>

